@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Turret+Road:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
  background-color: #357266;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}

.coin-search {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}

.coin-form {
  margin-bottom: 1rem;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  text-align: center;
  height: 50px;
  border-radius: 4px;
  font-size: 1.5rem;
  border: 1px solid #9883e5;
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

.sticky-info {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  padding-top: 20px;
}

.coin-info-table {
  display: flex;
  flex-direction: row;
  min-width: 1125px;
  justify-content: space-between;
  border-bottom: 2px solid #9883e5;
  padding-top: 10px;
  padding-bottom: 50px;
  height: 50px;
}

.coin-info-table h3 {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
}

.coin-name {
  margin-left: 120px;
}

.coin-shortname {
  color: #357266;
}

.change1H {
  padding-left: 25px;
}

.change1Y {
  margin-left: 15px;
}

.worth {
  margin-right: 35px;
}

.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-items: start;
  justify-content: center;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  min-width: 1050px;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 300px;
}

.coin img {
  height: 30px;
  margin-right: 10px;
  width: 30px;
}

.coin h1 {
  font-size: 16px;
  width: 150px;
  color: #9883e5;
}

.coin-symbol {
  text-transform: uppercase;
}

.coin-date {
  display: flex;
  text-align: center;
  justify-content: space-between;
  width: 100%;
}

.coin-price {
  width: 200px;
}

.coin-volume {
  width: 240px;
}

.coin-marketcap {
  width: 155px;
}

.coin-worth {
  width: 155px;
  color: #ffd700;
}

.coin-percent {
  width: 120px;
}

.red {
  color: #f00606;
  /* padding-top: 10px; */
  margin-right: 40px;
}

.green {
  color: #11d811;
  /* padding-top: 10px; */
  margin-right: 40px;
}

footer {
  border-top: 1px solid #fff;
  /* background-color: #0e1a27; */
  width: 100%;
  min-height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  text-align: center;
}

.footer {
  flex-shrink: 0;
  padding: 5px 0;
}

.footer span {
  font-size: 12px;
}

.footer-links {
  display: flex;
  align-content: center;
  justify-content: center;
}

.footer-links a {
  text-decoration: none;
  font-size: 30px;
  justify-self: center;
  align-self: center;
  color: #9883e5;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  background-color: #2e294e;
  width: 100%;
  height: 100px;
  top: 0;
  z-index: 99;
  margin-bottom: 1rem;
}

.logo-name h1 {
  background-color: #2e294e;
  font-family: "Turret Road", cursive;
  margin: 0 auto;
}

.icon-header svg {
  font-size: 60px;
  margin: 0;
  padding-top: 15px;
  background-color: #2e294e;
}

.icon-header {
  color: #9883e5;
  font-size: 60px;
  margin: 0;
  padding: 0;
  background-color: #2e294e;
}

